/* eslint-disable jsx-a11y/no-autofocus */
import React, { useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {
  Menu,
  Icon,
  Input,
  Button,
  Container,
  Message,
  Image,
} from 'semantic-ui-react'
import { toggleUnit, searchCountriesByName } from '../reducers/countryReducer'
import '../assets/css/App.css'
import flag from '../assets/images/ua.svg'

const HeaderNav = () => {
  const [value, setValue] = useState('')
  const unit = useSelector((state) => state.unit.unit)

  const state = useSelector((state) => state)

  const filtered = useSelector((state) => state.countries.filtered)

  const dispatch = useDispatch()

  useEffect(() => {
    if (filtered.length === 250) {
      setValue('')
    }
  }, [filtered])

  const handleInput = (e) => {
    e.preventDefault()
    const input = e.target.value
    dispatch(searchCountriesByName(input))
    setValue(input)
  }

  const noMatch =
    state.countries.filtered.length === 0 && state.countries.filter.input !== ''

  const clearInput = () => {
    dispatch(searchCountriesByName(''))
    setValue('')
  }

  const changeUnit = (val) => {
    dispatch(toggleUnit(val))
  }

  return (
    <>
      <Menu attached="top" inverted borderless>
        <Menu.Item header>
          <Icon className="App-logo" name="globe" color="teal" size="big" />
          <p> World Countries </p>
        </Menu.Item>
        <Menu.Item>
          <Input
            icon={
              <Icon
                link
                name={value.length > 0 ? 'close' : 'search'}
                onClick={value.length > 0 ? clearInput : handleInput}
              />
            }
            type="search"
            value={value}
            placeholder="Search by name"
            onChange={handleInput}
            autoFocus
          />
        </Menu.Item>
        <Menu.Item>
          <Image
            as="a"
            href="https://supportukrainenow.org/"
            alt="ukraine flag"
            srcSet={`${flag} 100w`}
            size="tiny"
            src={flag}
          />{' '}
        </Menu.Item>
        <Menu.Item>{filtered.length} records found</Menu.Item>
        <Menu.Item>
          <Button
            basic
            color="yellow"
            inverted
            content={unit === 'metric' ? 'imperial' : 'metric'}
            onClick={() =>
              changeUnit(unit === 'metric' ? 'imperial' : 'metric')
            }
          ></Button>
        </Menu.Item>
      </Menu>
      {noMatch ? (
        <Container>
          <Message compact info>
            <Message.Header>No matches, please try again.</Message.Header>
            <Button basic color="teal" onClick={clearInput}>
              OK
            </Button>
          </Message>
        </Container>
      ) : (
        <></>
      )}
    </>
  )
}

export default HeaderNav
